'use client'
import PageTitle from "@/components/PageTitle"
import { useEffect, useState } from "react";
import OrderItem from "@/components/OrderItem";
import { useAuthStore } from '@/stores/authStore'
import { authFetch } from '@/lib/auth'
import Loading from "@/components/Loading"

const API_BASE_URL = 'https://dark-caldron-448714-u5.uc.r.appspot.com/api'

export default function Orders() {
    const { user } = useAuthStore()
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchUserOrders = async () => {
            if (!user?.id) {
                setOrders([]);
                setLoading(false);
                return;
            }

            try {
                setError(null);
                const response = await authFetch(`${API_BASE_URL}/orders`);
                if (response.ok) {
                    const data = await response.json();
                    setOrders(data || []);
                } else {
                    throw new Error('Failed to fetch orders');
                }
            } catch (err) {
                console.error('Error fetching orders:', err);
                setError(err.message);
                setOrders([]);
            } finally {
                setLoading(false);
            }
        };

        fetchUserOrders();
    }, [user?.id]);

    if (loading) return <Loading />

    if (error) {
        return (
            <div className="min-h-[80vh] mx-6 flex items-center justify-center text-center">
                <div>
                    <h1 className="text-2xl sm:text-4xl font-semibold text-red-500 mb-4">Error Loading Orders</h1>
                    <p className="text-slate-600 mb-4">{error}</p>
                    <button
                        onClick={() => window.location.reload()}
                        className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition-colors"
                    >
                        Try Again
                    </button>
                </div>
            </div>
        )
    }

    return (
        <div className="min-h-[70vh] mx-6">
            {orders.length > 0 ? (
                <div className="my-20 max-w-7xl mx-auto">
                    <PageTitle heading="My Orders" text={`Showing total ${orders.length} orders`} linkText={'Go to home'} />

                    <table className="w-full max-w-5xl text-slate-500 table-auto border-separate border-spacing-y-12 border-spacing-x-4">
                        <thead>
                            <tr className="max-sm:text-sm text-slate-600 max-md:hidden">
                                <th className="text-left">Product</th>
                                <th className="text-center">Total Price</th>
                                <th className="text-left">Address</th>
                                <th className="text-left">Status</th>
                            </tr>
                        </thead>
                        <tbody>
                            {orders.map((order) => (
                                <OrderItem order={order} key={order._id || order.id} />
                            ))}
                        </tbody>
                    </table>
                </div>
            ) : (
                <div className="min-h-[80vh] mx-6 flex items-center justify-center text-center">
                    <div>
                        <h1 className="text-2xl sm:text-4xl font-semibold text-slate-400 mb-2">
                            {user ? 'You have no orders' : 'Please log in to view your orders'}
                        </h1>
                        {!user && (
                            <p className="text-slate-600">Sign in to see your order history</p>
                        )}
                    </div>
                </div>
            )}
        </div>
    )
}